#main-wrapper {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 920px;
    background-color: #F3EEEE;
    overflow: hidden;
}

#main-header-container {
    position: relative;
    top: 81px;
    padding: 0;
    margin: auto;
    width: 87%;
    height: 160px;
   
    display: flex;
}

#main-header-title {
    position: relative;
    top: 33px;
    font-family: robolight;
    font-size: 27px;
    color: black;
    margin: 0;
    padding: 0;
    font-weight: 800;
    font-style: normal;
    padding-left: 45px;
}

#main-header-desc {
    position: relative;
    top: 36px;
    font-family: robolight;
    font-size: 17px;
    color: black;
    margin: 0;
    padding: 0;
    padding-left: 45px;
}

#main-status-checker-container {
    position: relative;
    top: 118px;
    height: 155px;
    width: 87%;
    margin: auto;
    padding: 0;
  
    
}

#main-status-checker-box {
    height: 155px;
    width: 80%;
    border: solid 1px black;
}

#main-status-nav-bar {
    width: 100%;
    height: 56px;
    background-color: #5680E9;
    margin: 0px 0px;
    padding: 0;
}

#main-status-nav-bar > h1 {
    position: relative;
    top: 17px;
    padding-left: 25px;
    margin: 0px 0px;
    
    font-size: 17px;
    color: white;
    font-family: robolight;
    font-weight: 100;
}

#main-status-fields-flex {
    display: flex;

    height: 100px;
}

#main-status-checker-input {
    
    position: relative;
    top: 27px;
    margin: 0;
    padding: 0;
    border: none;
    height: 43px;
    width: 45%;
    background-color: white;
    padding-left: 25px;
    margin-left: 25px;
    font-family: robolight;
}

#main-status-checker-btn {
    
    position: relative;
    top: 27px;
    margin: 0;
    padding: 0;
    border: none;
    float: right;
    height: 43px;
    width: 109px;
    background-color: #5680E9;
    font-family: robolight;
    font-size: 17px;
    color: white;
    cursor: pointer;
}

#main-status-data-grid {
    margin: 0;
    padding: 0;
    position: relative;
    top: 37px;
    width: 100%;
    height: 404px;
    border: solid 1px black;
}

#main-status-result-success {
    margin: 0;
    padding: 0;
    color: green;
    font-family: robolight;
    font-weight: 100;
    position: relative;
    top: 40px;
    padding-left: 20px;
    font-size: 15px;
}

#main-status-result-fail {
    margin: 0;
    padding: 0;
    color: red;
    font-family: robolight;
    font-weight: 100;
    position: relative;
    top: 40px;
    padding-left: 20px;
    font-size: 15px;
}

/* fetch loader animation */
.loader {
    display: inline-block;
    position: relative;
    top: 20px;
   
    width: 10%;
    height: 60px;
  }
  .loader div {
    display: inline-block;
    position: absolute;
    
    width: 16px;
    background-color: #5680E9;
    animation: loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  }
  .loader div:nth-child(1) {
    left: 8px;
    animation-delay: -0.24s;
  }
  .loader div:nth-child(2) {
    left: 32px;
    animation-delay: -0.12s;
  }
  .loader div:nth-child(3) {
    left: 56px;
    animation-delay: 0;
  }
  @keyframes loader {
    0% {
      top: 8px;
      height: 44px;
      width: 15px;
    }
    50%, 100% {
      top: 24px;
      height: 12px;
      width: 15px;
    }
  }
  

#tip-msg {
    font-size: 12px;
    font-family: robolight;
    position: absolute;
    top: 135px;
    margin: 0;
    padding: 0;
    padding-left: 25px;
    
}

#additional-details-wrapper {
    width: 100%;
    height: 347px;
  
}

#additionalDetails-btn {
    position: relative;
    top: 58px;
    margin: 0;
    padding: 0;
    width: 170px;
    height: 43px;
    border: none;
    font-family: robolight;
    color: white;
    background-color: #5680E9;
    cursor: pointer;
}

#caret {
    position: relative;
    top: -1px;
    margin: 0;
    padding: 0;
}

#additional-details-content {
    position: relative;
    top: 58px;
    margin: 0;
    padding: 0;
    
}

.detail-item {
    list-style-type: none;
    font-family: robolight;
    padding-top: 20px;
}

.bold-detail {
    font-family: robobold;
}

#copy-btn {
    width: 78px;
    height: 30px;
    background-color: #DC4B4B;
    color: white;
    font-family: robolight;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.green-text {
    color: green;
    font-family: robolight;
    font-size: 12px;
}

/* responsive */
@media(max-width: 681px) {
    .loader {
        position: absolute;
        top: -50px;
        width: 100%;
    }
}

@media(max-width: 527px) {
    #main-header-img {
        height: 100px;
    }

    #main-header-title {
        position: relative;
        top: 7px;
        font-size: 20px;
    }

    #main-header-desc {
        position: relative;
        top: 7px;
        font-size: 14px;
        width: 50%;
    }

    #main-status-nav-bar > h1 {
        top: 20px;
        font-size: 13px;
    }
}

@media(max-width: 471px) {
    #main-status-checker-input {
        height: 36px;
        width: 45%;
    }
    #main-status-checker-btn {
        height: 36px;
        width: 23%;
        font-size: 15px;
    }
    
}

@media(max-width: 383px) {
    .desc-extra {
        display: none;
    }
}
  
/* fonts */

@font-face {
    font-family: robobold;
    src: url("./fonts/Roboto-Bold.ttf");
}

@font-face {
    font-family: robolight;
    src: url("./fonts/Roboto-Light.ttf");
}