#navigation-wrapper {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 80px;
    background-color: #2C2D2D;
    display: block;
}

#nav-brand-container {
    display: flex;
    width: 95%;
    height: 80px;
    
    margin: auto;
}

#brand-image {
    position: relative;
    top: 20px;
    margin: 0;
    padding: 0;
}

#site-name {
    position: relative;
    top: 30px;
    margin: 0;
    padding: 0;
    color: white;
    font-size: 20px;
    font-family: robobold;
    padding-left: 26px;
}

/* fonts */

@font-face {
    font-family: robobold;
    src: url("./fonts/Roboto-Bold.ttf");
}

@font-face {
    font-family: robolight;
    src: url("./fonts/Roboto-Light.ttf");
}